import React, { useState, useEffect } from 'react';
import ArticlesList from '../components/ArticlesList'
import articleContent from './article-content';
import NotFound from './404NotFound';
import CommentsList from '../components/CommentsList';
import AddCommentForm from '../components/AddCommentForm';
import UpvotesSection from '../components/UpvotesSection';

const ArticlePage = ({ match }) => {
    const name = match.params.name;
    const article = articleContent.find(article => article.name === name);

    const [articleInfo, setArticleInfo] = useState({ 
        upvotes: 0,
        comments: []
    });

    useEffect(() => {
        const fetchData = async () => {
            const result = await fetch(`/api/articles/${name}`);
            const body = await result.json();
            console.log(body);
            setArticleInfo(body); // set Article Info using response body from server
        }
        fetchData();
    }, [name]); //this is an array of values useEffect should watch.
    //If this isn't here, the upvotes number will be continually updated,
    // however, when this is MT, it never updates, we need to let it know what changes
    //when we change pages, and so useEffect will change whenever the page changes

    if (!article) return <NotFound />
    
    const otherArticles = articleContent.filter(article => article.name !== name);

    return (
        <>
        <h1>{article.title}</h1>
        <UpvotesSection articleName={name} upvotes={articleInfo.upvotes} setArticleInfo={setArticleInfo} />
        {article.content.map((paragraph, key) => (
            <p key={key}> {paragraph} </p>
        ))}
        <CommentsList comments={articleInfo.comments} />
        <AddCommentForm articleName={name} setArticleInfo={setArticleInfo} />
        <h2>Other Articles</h2>
        <ArticlesList articles={ otherArticles }/>
        </>
    );
}

export default ArticlePage;